body {
    display: grid;
    grid-template-rows: 100px auto;
    align-items: stretch;
    justify-items: stretch;
    justify-content: stretch;
    align-content: stretch;
    font-family: Arial, Helvetica, sans-serif;
    background-color: #101010;
    color: #ffffff;
    margin: 0;
}

h1 {
    font-weight: lighter;
    background-color: #202020;
    margin: 0;
    text-align: center;
    padding-top: 1em;
}

h1 .bold {
    font-weight: 700;
}